/* #88d8bf */

/* APP CONTAINER */
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
    Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  color: #333;
}

a {
  font-weight: 300;
  color: #333;
}

/* TEXT */
.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

/* BORDERS */
.border-rounded {
  border-radius: 5px;
}

.border-top {
  border-top: 1px solid #d7d7d7;
}

.border-bottom {
  border-top: 1px solid #d7d7d7;
}

.border-all {
  border: 1px solid #d7d7d7;
}

/* FOR TESTING */
.test {
  border: 1px solid red;
}

.test2 {
  border: 1px solid blue;
}

/* CONTAINERS */
.route-container {
  margin-top: 25px;
  margin-bottom: 50px;
}

/* COMPONENT SPECIFIC */
input[type='text'] {
  height: 40px;
  border: 2px solid #88d8bf;
  max-width: 200px;
}

textarea {
  height: 100px;
  max-width: 200px;
  resize: none;
  border: none;
  outline: 2px solid #88d8bf;
}

button {
  width: 200px;
  border-radius: 0px;
}

/* REACT COMPONENTS */
.feed-image {
  max-width: 600px;
  /* insta stndard */
}

.profile-image-container {
  width: 75px;
  height: 75px;
}
.profile-image {
  border-radius: 50%;
  width: 75px;
  height: 75px;
}

.product-image {
  width: 100vw;
  max-width: 600px;
  height: 100vw;
  max-height: 600px;
}

.error-text {
  height: 20px;
  margin: 0;
  padding: 0;
  color: red;
}

.spinner {
  -webkit-animation: spin 4s linear infinite;
          animation: spin 4s linear infinite;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* LINKS */
.top-nav-link {
  height: 25px;
}



